import {Component,Inject,forwardRef} from '@angular/core';
import { Router }      from '@angular/router';
import {HttpClient, HttpClientModule} from "@angular/common/http";
import {DOCUMENT} from "@angular/common";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html'
})
export class LoginComponent {
  message: string;
  username: string;
  password: string;

  constructor( public router: Router, private  http :HttpClient, @Inject(DOCUMENT) private document: any ) {
  }



  register(){
    this.router.navigate(["/register"]);
  }



  login(): any {

    this.http.post('api/login',{username:this.username,password:this.password},{ observe: 'response', responseType:'text' },).subscribe(data => {
        console.log(data);
        let token = data.headers.get('Authorization');
        console.log(token);
        localStorage.setItem('currentUser', token );
      }, error =>{
      console.log(error);

      }
    );

  }



  goToUrl(): void {
    this.document.location.href = 'http://localhost:8080/';
  }


}



